h4 {
    font-size: 16px;
    font-weight: bold;
}

.bottom-gallery {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-top: 5vh;
    margin-bottom: 5vh;
}

.header-img {
    width: 50vw;
    border-radius: 5%;
}

.heading {
    margin: 1vh;
    margin-bottom: 2vh;
}

.gallery-img {
    width: 16vw;
    border-radius: 5%;
}

.post-container {
    margin-left: 15vw;
    margin-right: 15vw;
    margin-top: 5vh;
    margin-bottom: 5vh;
    padding: 3vw;
    padding-left: 10vw;
    padding-right: 10vw;
    background-color: #c1e9e4;
    border-radius: 3%;
    box-shadow: 17px 17px #679a9c;
    /* max-width: 800px; */
}

.post-body {
    text-align: left;
}



